import * as React from "react"
import { HeadFC, PageProps } from "gatsby"
import * as styles from "../styles/index.module.scss"
import { useQuery } from "react-query";
import { getWPGraphQLData } from "../api/my-api"
import AFC from "../images/icons/Icon_AFC.png"
import Chunk from "../components/chunk";
import { chunkData } from "../api/my-api";
import AboutAFC from "../components/about-afc";

const IndexPage: React.FC<PageProps> = () => {
  const WP_GQL_QUERY = `
    query {
      menuItems {
        nodes {
          label
        }
      }
    }
  `;

  const { isLoading, isError, data, error } = useQuery('getHomeContent', () => getWPGraphQLData(WP_GQL_QUERY))

  return (
    <div className={`page-wrapper`}>
      <header className={`relative grid items-center justify-center h-screen mb-12 overflow-hidden ${styles.hero}`}>
        <video autoPlay muted loop className="absolute z-1 w-auto min-w-full min-h-full object-cover">
          {/* <source src="https://dm0qx8t0i9gc9.cloudfront.net/watermarks/video/qmraJpx/three-woman-exercise-on-equipment-at-a-gym-low-angle_hy-mfsno__f5d48777d59dd71126f85670ab1fc960__P360.mp4" type="video/mp4"/> */}

          <source src="https://dm0qx8t0i9gc9.cloudfront.net/watermarks/video/EoeqFzsdgijpphbw0/videoblocks-sporty-woman-trains-squats-with-heavy-weights-in-fitness-gym_sbqp6u9f0x__8fc88442caf8d1533efcb6a6b9dd6206__P360.mp4" type="video/mp4" />
        </video>

        <article className="z-30 p-5 text-xl text-white bg-opacity-50 max-w-4xl">
          <h1>
            <figure className="grid justify-center">
              <img src={AFC} className={''} />
            </figure>
            TRAIN WITH THE BEST
          </h1>

          <p className="p-4">
            When it comes to fitness, Alliance Fitness Center knows you have particular needs, frustrations, and preferences. One reason for our high client success rate is that we turn each member's distinctiveness into a training advantage.
          </p>
        </article>
      </header>

      <section className={styles.spacer} />

      <main className={`main`}>
        <header>
          <h2 className="font-bold text-3xl">WHAT WE OFFER AT ALLIANCE FITNESS CENTER</h2>
          <p className="sm:text-sm md:text-base md:leading-5 lg:text-lg lg:leading-6">
            Alliance Fitness Trainers are masters at creating specialized programs that align with your goals while focusing on your overall fitness experience. We ensure that fitness becomes a part of your lifestyle and that you enjoy yourself in the process.
          </p>
        </header>

        <div>
          {
            chunkData.map((data) => {
              return (
                <Chunk
                  key={data.id}
                  header={data.header}
                  summary={data.summary}
                  image={data.image}
                  flip={data.flip}
                  arrange={data.arrange}
                  pathStyle={data.pathStyle}
                  id={data.id}
                  offset={data.offset}
                  icon={data.icon}
                />
              )
            })
          }
        </div>

        <AboutAFC/>
      </main >
    </div >
  )
}

export default IndexPage
export const Head: HeadFC = () => <title>Home Page</title>
