import React, { useEffect, useState } from "react";
import * as styles from "../styles/reviews.module.scss"
import { reviews } from "../api/my-api";
import Card from "./card";

const Reviews = () => {
    const [randomReviews, setRandomReviews] = useState([])

    // A function to generate a random
    // permutation of arr
    const randomize = (arr: any) => {
        // Start from the last element and swap
        // one by one. We don't need to run for
        // the first element that's why i > 0
        for (let i = arr.length - 1; i > 0; i--) {

            // Pick a random index from 0 to i inclusive
            let j = Math.floor(Math.random() * (i + 1));

            // Swap arr[i] with the element
            // at random index
            [arr[i], arr[j]] = [arr[j], arr[i]];
        }
        return arr;
    }

    useEffect(() => {
        setRandomReviews(randomize(reviews))
    }, [])

    return (
        <div className="grid place-content-center">
            <header className="grid justify-center mb-14">
                <h2 className="font-bold text-3xl text-yellow">TESTIMONIALS</h2>
            </header>
            <div className="masonry md:masonry-md lg:masonry-lg max-w-7xl">
                {
                    randomReviews.map((review: any, key: any) => {
                        return (
                            <Card key={key}>
                                <figure className="grid grid-cols-[auto_1fr] gap-4 items-center">
                                    <img src={review.reviewer.profilePhotoUrl} alt="" />
                                    <figcaption>
                                        <h3>{review.reviewer.displayName}</h3>
                                        <span>{review.starRating}</span>
                                    </figcaption>
                                </figure>

                                <blockquote className="font-light sm:text-sm md:text-base lg:text-lg" dangerouslySetInnerHTML={{ __html: review.comment }} />
                            </Card>
                        )
                    })
                }
            </div>
        </div>
    )
}

export default Reviews
